.game_over-tab {
  position: absolute;
  left: 35%;
  top: 40%;
  width: 30%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 1000;
}

.rocketBox {
  background-color: #9535a6;
}
.ballDestroyerBox {
  background-color: #35a684;
}
.untouchableBox {
  background-color: #2e2e2e;
  color: #fff;
}
.magneticTrapBox {
  background-color: #203eff;
}
.active_rocket {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  transition: transform 0.2s ease;
}
.control_panel {
  width: 100%;
  height: 10.5vh;
  background-image: linear-gradient(90deg, #3943DE, #442C7B);
}
.bottom_game_zone_panel {
  background-image: url('../../popups/sources/control-panel-bg.png');
  background-size: 100% 100%;
  width: 47.7vh;
  height: 7vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.gradient_mock {
  width: 100%;
  height: 100%;
  max-height: 8vh;
  background-image: linear-gradient(90deg, #3943DE, #442C7B);
}
.element span {
  color: #FFFFFFE5;
  font-weight: bold;
  text-shadow: 
    -1px -1px 0 #303D6099, /* Верхний левый */
    1px -1px 0 #303D6099,  /* Верхний правый */
    -1px 1px 0 #303D6099,  /* Нижний левый */
    1px 1px 0 #303D6099;   /* Нижний правый */
}
.bottom_game_zone_panel-no_active {
  width: 39.4vh;
}
.scroll_control_button {
  border: none;
  background-color: transparent;
  background-image: url('../../img/elements/controller.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transform: translateY(-2vh);
  height: 100%;
}
.control_panel {
  display: flex;
  align-items: center;
  justify-content: center;
}
.control_panel .control_panel-btn {
  width: 5vh;
  height: 5vh;
  background-color: #FFFFFF26;
  border: #95A3C94A 1px solid;
  margin: 0 .5vh;
}
.control_panel .control_panel-btn .control_panel-icon {
  width: 100%;
  height: 100%;
}
.stars_indicator_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  width: 20vh;
  height: 5vh;
  margin: 0 1.5vh;
}
.star_indicator {
  width: 90%;
  height: 2vh;
  background-image: linear-gradient(90deg, #3943DE, #442C7B);
  border: #95A3C94A 1px solid;
  display: flex;
  align-items: center;
}
.star_indicator .star_indicator-progress {
  height: 90%;
  width: 10%;
  margin-left: .1vh;
  background-image: linear-gradient(#FED002 60%, #FFFFFF);
}
.stars_markers {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.stars_markers .star_element {
  height: 2vh;
}