.collections_popup-title {
  width: 60%;
  border: 1vh solid;
  border-image: linear-gradient(90deg, #5DFFA4, #517FFB, #00FFE0); /* Градиент для обводки */
  border-image-slice: 1;
}
.collections_popup_tab .popup-nav_panel-nav_element.active {
  color: #01FFE0;
  text-shadow: 0 0 1vh #01FFE0;
}
.collections_popup_container .close_tab_btn {
  filter: drop-shadow(0 0 1.8vh #01FFE0);
}
.skin_collection_element,
.trail_collection_element,
.background_collection_element {
  border: .3vh solid;
  border-image: linear-gradient(#02FADC, #06CFB74A 30%);
  border-image-slice: 1;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.skin_collection_element-active,
.trail_collection_element-active,
.background_collection_element-active {
  border: .3vh solid #02FADC;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.skin_collection_element-buy_btn,
.trail_collection_element-buy_btn,
.background_collection_element-buy_btn {
  background-color: #3D0E82E5;
  border: none;
  border-top: #6d3ade .2vh solid;
}