.game_mods_list-container {
  width: 60%;
  height: 80%;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.adds_tab {
  width: 100%;
  height: 12vh;
  background-color: #220663;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 3vh;
  padding-bottom: 2vh;
}
.adds_tab-game {
  width: 100%;
  height: 12vh;
  background-image: linear-gradient(90deg, #3943DE, #442C7B);
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 3vh;
  padding-bottom: 2vh;
}
.adds_tab .adds_subtab {
  width: 90%;
  height: 7vh;
  background-color: #FFFFFF26;
  border: .1vh solid;
  border-image: linear-gradient(#623fb3, #00000000);
  border-image-slice: 1;
}
.adds_tab-game .adds_subtab {
  width: 90%;
  height: 7vh;
  background-color: #FFFFFF26;
  border: .1vh solid;
  border-image: linear-gradient(#aab9fe, #00000000);
  border-image-slice: 1;
}

.globalBody {
  width: 100vw;
  height: 87vh;
  position: relative;
  background-image: linear-gradient(#3D0E82,#3D0E82, #451086, #591591, #7C1EA2, #AB29BA, #CD32CC);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

body {
  background: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 98vh;
  /* Запрет выделения текста для всего сайта */
  user-select: none; /* Современные браузеры */
  -webkit-user-select: none; /* Для Safari */
  -moz-user-select: none; /* Для Firefox */
  -ms-user-select: none; /* Для Internet Explorer и Edge */
  width: 100%;
  height: 100%;
  overflow: hidden;
  overscroll-behavior: none;
}

.classic_mode {
  height: 100%;
  width: 100%;
}
.game_mode-tab {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

span {
  user-select: none;
}

#container {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  width: 38vh;
  height: 62vh;
  border: 1.5vh solid;
  border-left: 0.7vh solid;
  border-right: 0.7vh solid;
  border-image: linear-gradient(#3D0E82 -30%, #5283FF);
  border-image-slice: 1;
  background-color: #3D0E82;
  -webkit-box-shadow: 0px 5vh 3vh 1vh #3D0E82; 
  box-shadow: 0px 5vh 3vh 1vh #3D0E82;
}

#waiting-container,
#stopped-container {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}


#waiting-counter,
#stopped-counter {
  position: absolute;
  top: -300%;
  border-radius: 50%;
  padding: 2px 5px;
  white-space: nowrap;
  text-transform: uppercase;
  color: #FFFFFF;
}

.ball {
  position: absolute;
  border-radius: 100%;
  background-image: url('./app/img/balls/classic.png');
  background-size: 100% 100%;
  z-index: 2;
}

.element {
  position: absolute;
}

.box {
  background-image: linear-gradient(#00FED8, #00FED8);
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 95% 95%;
}

.triangle {
  position: absolute;
  width: 0;
  height: 0;
}

.bonus-box {
  display: flex;
  align-items: center;
  justify-content: center;
}

.circle-bonus {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 50%;
  height: 50%;
}

.bonus-balls-circle {
  background-color: #690101;
}

.bonus-balls-circle>span {
  color: #FFFFFF;
}

.laser-circle {
  background-color: #009002;
}

.laser-circle>span {
  color: #FFFFFF;
}


.counter_container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}

.counter_container>span {
  width: 50%;
  text-align: center;
}

/* Positioning for different triangle orientations */
.triangle-RB .counter_container {
  transform: translateX(-100%);
}

/* 
.triangle-LB .counter_container {
  
} */

.triangle-RT .counter_container {
  transform: translate(-100%, -100%);
}

.triangle-LT .counter_container {
  transform: translateY(-100%);
}


.triangle-RB .counter_container>span {
  transform: translate(50%, 50%);
}

.triangle-LB .counter_container>span {
  transform: translate(-50%, 50%);
}

.triangle-RT .counter_container>span {
  transform: translate(50%, -50%);
}

.triangle-LT .counter_container>span {
  transform: translate(-50%, -50%);
}


.moving {
  transition: all .1s linear;
}

#stop-button {
  margin-top: 10px;
}

.trajectory-segment {
  position: absolute;
  /* border-top: 1px dashed #FFFFFF80; */
  background-color: #FFFFFF80;
  border-radius: 50%;
}

.bottom_game_zone_panel {
  width: 40vh;
  height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bottom_game_zone_panel-no_active {
  display: flex;
  height: 100%;
  width: 100%;
}

.bonuses_panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}

.bonuses_panel-bonus {
  width: 12%;
  height: 50%;
  border: none;
  background-color: transparent;
  background-image: url('./app//img/elements/control-btn.png');

  background-size: 100% 100%;
}

.scroll_control_button {
  width: 15%;
}

.scroll_panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.laser-container {
  position: absolute;
  background-color: lightgreen;
  opacity: 0.5;
  z-index: 10;
}

.last_step_warning_zone {
  display: none;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3vh;
  background-color: #f9222276;
}

.lang-test-button {
  position: absolute;
  left: 0;
  top: 0;
}

.trail {
  position: absolute;
  background-image: url('./app/img/trails/classic.png');
  background-size: 100% 100%;
  border-radius: 50%;
  pointer-events: none;
  animation: fadeOutTrail 0.5s forwards;
  z-index: 1;
}

@keyframes fadeOutTrail {
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}


/* Портретный режим: контейнер занимает весь экран */
@media (orientation: portrait) {
  .globalBody {
    width: 100vw;
    height: 100vh;
  }
}

/* Альбомный режим: высота 100%, ширина 50% высоты */
@media (orientation: landscape) {
  .adds_tab {
    width: 50vh;
  }
  .globalBody {
    height: 100vh;
    width: 50vh; /* ширина равна половине высоты */
  }
}