.content_container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.quick_play_container {
  position: absolute;
  height: 55vh;
  width: 35vh;
  bottom: -3.5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.quick_play-arcade_game {
  height: 2.5vh;
  width: 25vh;
  position: relative;
  margin-top: 3.9vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quick_play-arcade_game .arcade_game_text{
  height: 1.4vh;
  position: absolute;
}
.game_logo {
  margin-top: 8.5vh;
  width: 40%;
}
.quick_play_btn {
  color: #FFFFFF;
  background-image: linear-gradient(#8DFF00 0%, #00FF66 100%);
  width: 21vh;
  height: 8vh;
  margin-top: 15vh;
  border: none;
  -webkit-box-shadow: 10px 20px 50px -30px #1EE9324D; 
  box-shadow: 10px 20px 50px -30px #1EE9324D;
  text-transform: uppercase;
  z-index: 10;
  font-size: 3vh;
  text-shadow: 
    -1px -1px 0 #28086580, /* Верхний левый */
    1px -1px 0 #28086580,  /* Верхний правый */
    -1px 1px 0 #28086580,  /* Нижний левый */
    1px 1px 0 #28086580;   /* Нижний правый */
}
.quick_play_btn_after {
  width: 21vh;
  height: 8vh;
  margin-top: -7.6vh;
  background-image: linear-gradient(#80C727 0%, #23211B 100%);
  z-index: 5; /* Псевдоэлемент окажется под кнопкой */
  -webkit-box-shadow: inset 0px 0px 10px 5px #FFFFFF80; 
  box-shadow: inset 0px 0px .5vh .2vh #FFFFFF80;
}
.app_navigation {
  width: 100%;
  height: 13vh;
  background-color: #220663;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  border-top: #6F3BE2 .3vh solid;
}
.app_navigation-gradient_hr {
  width: .5%;
  height: 100%;
  background-image: linear-gradient(90deg, #6F3BE2 0%, #220663CC 80%);
}
.app_navigation-menu_btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 25%;
  height: 100%;
  background-color: #FFFFFF10;
  border: none;
}
.app_navigation-menu_btn img {
  height: 6vh;
  margin-bottom: .5em;
}
.app_navigation-menu_btn span{
  text-transform: uppercase;
  font-weight: bold;
  color: #FFFFFF;
  letter-spacing: .2cap;
  font-size: 1.2vh;
}
.profile_container {
  position: absolute;
  top: 0vh;
  left: 2vh;
  width: 9vh;
  height: 11vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.daily_task_container {
  position: absolute;
  top: 13vh;
  left: 2vh;
  width: 9vh;
  height: 11vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.map_btn {
  position: absolute;
  bottom: 4vh;
  left: 3vh;
  width: 7vh;
  height: 7vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: none;
  background-image: linear-gradient(220deg,#D2FFBE 0%, #FF52DC 35%, #CD0082 65%,#4C00C1 100%);
}

.map_btn img{
  height: 70%;
}
.profile_icon_container{
  width: 7vh;
  height: 7vh;
  background-image: linear-gradient(120deg,#00FFA0 0%, #3C6DEB 100%);
  overflow: hidden;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.daily_task_icon_container {
  width: 7vh;
  height: 7vh;
  background-image: linear-gradient(120deg, #3142E4 0%, #220663 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.daily_task_icon_subcontainer {
  width: 6.6vh;
  height: 6.6vh;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.profile_icon_subcontainer {
  width: 6.6vh;
  height: 6.6vh;
  overflow: hidden;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.profile_icon_container img {
  height: 100%;
}
.daily_task_icon_container img{
  height: 100%;
  margin-top: -1vh;
}
.absolute_text {
  color: #FFFFFFE5;
  font-weight: bold;
  text-shadow: 
    -1px -1px 0 #303D6099, /* Верхний левый */
    1px -1px 0 #303D6099,  /* Верхний правый */
    -1px 1px 0 #303D6099,  /* Нижний левый */
    1px 1px 0 #303D6099;   /* Нижний правый */
}
.player_name {
  font-size: 1.5vh;
  margin-top: .5vh;
}
.daily_task_title {
  font-size: 1.4vh;
  margin-top: .5vh;
  text-transform: uppercase;
  white-space: nowrap;
}
.daily_task_timer_container {
  margin-top: -0.6vh;
  background-image: linear-gradient(90deg, #FE54E7 0%, #8E58FB 50%, #EA038C 100%);
  display: flex;
  align-items: center;
  justify-content: center;
}
.daily_task_timer_subcontainer {
  margin: 1px;
  width: 5vh;
  background-image: linear-gradient(90deg, #3142E4 0%, #1E2C83 100%);
  border: #25319E 1px solid;
  font-size: 1.2vh;
  text-align: center;
}
.settings_panel {
  position: absolute;
  right: 2vh;
  top: 7.2vh;
}
.settings_panel .settings_panel-btn{
  width: 5vh;
  height: 5vh;
  background-color: #FFFFFF26;
  border: #95A3C94A 1px solid;
}
.settings_panel .settings_panel-btn {
  margin-left: 1vh;
}
.settings_panel .settings_panel-btn .settings_panel-icon{
  width: 100%;
  height: 100%;
}
.popup_tab {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #220663d8;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_container {
  background-color: #432b78;
  margin-top: 20vh;
  height: 71vh;
  width: 90%;
  border: .3vh solid;
  border-image: radial-gradient(#FFFFFF, #15151500, #FED0034A); /* Градиент для обводки */
  border-image-slice: 1;
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.popup_container .close_tab_btn {
  cursor: pointer;
  position: absolute;
  top: .5vh;
  right: .5vh;
  width: 3vh;
  height: 3vh;
  border: none;
  color: #432b78;
  background-color: transparent;
  font-size: 1.2vh;
  filter: drop-shadow(0 0 1.8vh #fed002);
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup_container .close_tab_btn img{
  width: 100%;
  height: 100%;
}
.popup-title {
  font-size: 2vh;
  color: #FFFFFF;
  font-family: "Tiny5", sans-serif;
  font-weight: 400;
  padding: .3vh 1vh;
  width: 35%;
  font-style: normal;
  text-transform: uppercase;
  background-image: linear-gradient(90deg, #3943DE 0%, #222A71 100%);
  text-align: center;
  margin-top: -3vh;
}
.popup-title span{
  font-size: 3vh;
  letter-spacing: .3ch;
  color: #FFFFFF;
  font-family: "Tiny5", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
}
.cash_panel {
  position: absolute;
  top: 1vh;
  right: 2vh;
  z-index: 110;
  display: flex;
}
.cash_panel .cash_container {
  width: 12vh;
  margin-left: 1.5vh;
  padding: .1vh .2vh;
  height: 3vh;
  background-color: #432b78;
  border: #95A3C94A 1px solid;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cash_panel .cash_container span{
  font-size: 2vh;
  margin: 0 .5vh;
  width: 5vh;
}
.buy_cash_btn {
  background-color: #8DFE01;
  width: 3vh;
  height: 3vh;
  border: none;
  border-bottom: #476D43 2px solid;
  font-size: 2.7vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.star_icon {
  width: 2.5vh;
}
.gem_icon {
  width: 3vh;
}
.popup-nav_panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 1.5vh;
}
.popup-nav_panel .popup-nav_panel-nav_element {
  text-transform: uppercase;
  font-size: 1.7vh;
  font-weight: 500;
  letter-spacing: .2vh;
}
.collection_tab {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 1vh;
  grid-row-gap: 2vh;
  width: 90%;
  margin-top: 1vh;
}

.collection_element {
  background-image: url('../../popups/sources/bп-gradients2.png');
  background-size: 100% 100%;
  width: 100%;
}
.collection_element-img {
  height: 8vh;
  margin-bottom: .5vh;
}
.collection_element-active_marker,
.collection_element-buy_btn {
  width: 100%;
  height: 4vh;
  display: flex;
  align-items: center;
}
.collection_element-buy_btn {
  justify-content: space-evenly;
}
.collection_element-active_marker {
  justify-content: flex-end;
}
.collection_element-active_marker img{
  height: 100%;
}
.collection_element-title {
  margin-bottom: 1vh;
  letter-spacing: .1vh;
  font-size: 2vh;
}
.collection_element-price {
  font-size: 2.5vh;
}