.gun_platform {
  position: absolute;
  background-color: blueviolet;
}

.laser-bonus {
  background-color: #013609;
}
.fire-bonus {
  background-color: #e78f00;
  display: flex;
  align-items: center;
  justify-content: center;
}
.money {
  /* background-color: #009247; */
  background-image: url('./sourses/images/MoneyRhombus.png');
  background-size: 100%, 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cash_value,
.exp_value,
.level_container {
  color: #ffffff;
  font-size: 2rem;
}

.alert_popup {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0000007a;
  z-index: 100;
}

.gunElement {
  position: absolute;
  background-color: #42007c;
  z-index: 100;
}
.gun1Element,
.gun2Element,
.gun3Element,
.gun4Element,
.gun5Element {
  background-color: #42007c;
}

.choose_bonus_panel {
  display: flex;
  align-items: center;
  justify-content: center;
}
.laser-trajectory-segment {
  position: absolute;
  border-top: 2px dashed #00ffea;
}