.achievements-list {
    text-align: center;
    padding: 20px;
    background-color: #f9f9f9;
    font-family: Arial, sans-serif;
    height: 86vh;
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.achievements-container {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
    max-width: 1200px;
    overflow-y: auto;
    gap: 10px;
    padding: 10px;
}


h2 {
    font-size: 2rem;
    color: #2c3e50;
    margin-bottom: 20px;
}