.body {
    height: 100%;
    max-height: 100%;
    position: relative;
}

.button {
    padding: 10px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    margin-top: 10px;
    transition: background-color 0.3s ease;
}

.win-button {
    position: absolute;
    bottom: 200;
    right: 100;
}

.lose-button {
    position: absolute;
    bottom: 100;
    right: 100;
}