.mode_popup-title {
  border: 1vh solid;
  border-image: linear-gradient(
    90deg,
    #ff52dc,
    #8059ff,
    #cd0082
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.mode_popup_container .close_tab_btn {
  filter: drop-shadow(0 0 1.8vh #cd0082);
}
.mode_popup_container {
  border: 0.3vh solid;
  border-image: radial-gradient(#ffffff, #15151500, #f61bd14a);
  border-image-slice: 1;
}
.popup-mode_list {
  width: 90%;
  margin-top: 3vh;
}
.mode_list-mode {
  width: 100%;
  height: 11vh;
  margin-bottom: 1.5vh;
  display: flex;
  flex-direction: column;
  letter-spacing: 0.1vh;
  position: relative;
}
.infinite_mode_list-mode {
  background-image: url('../sources/infinite-bg.png');
  background-size: 100% 100%;
  border: 0.3vh solid;
  border-image: linear-gradient(
    175deg,
    #f3b6ffe9,
    #15151500,
    #c095c94a
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.gravity_mode_list-mode {
  background-image: url('../sources/gravity-bg.png');
  background-size: 100% 100%;
  border: 0.3vh solid;
  border-image: linear-gradient(175deg, #a7fff5, #15151500, #95c9b84a);
  border-image-slice: 1;
}
.hundred_mode_list-mode {
  background-image: url('../sources/hundred-bg.png');
  background-size: 100% 100%;
  border: 0.3vh solid;
  border-image: linear-gradient(
    175deg,
    #ffe998c0,
    #15151500,
    #D8AC0D4A
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.survival_mode_list-mode {
  background-image: url('../sources/survival-bg.png');
  background-size: 100% 100%;
  border: 0.3vh solid;
  border-image: linear-gradient(
    175deg,
    #63d970d2,
    #15151500,
    #00CA184A
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.classic_mode_list-mode {
  background-image: url('../sources/survival-bg.png');
  background-size: 100% 100%;
  border: 0.3vh solid;
  border-image: linear-gradient(
    175deg,
    #8bff8fe9,
    #15151500,
    #bfc9954a
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.mode_list-mode-mode_name {
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-image: url('../sources/modes-bg.png');
  background-size: 100% 180%;
}
.mode_list-mode-mode_name .mode-mode_name-title {
  text-transform: uppercase;
  font-size: 2.5vh;
  margin-right: 2vh;
}
.infinite_mode_list-mode .mode_list-mode-mode_name .mode-mode_name-title {
  color: #ff45e1;
}
.classic_mode_list-mode .mode_list-mode-mode_name .mode-mode_name-title {
  color: #80FF01;
}
.gravity_mode_list-mode .mode_list-mode-mode_name .mode-mode_name-title {
  color: #00FED8;
}
.hundred_mode_list-mode .mode_list-mode-mode_name .mode-mode_name-title {
  color: #FED002;
}
.survival_mode_list-mode .mode_list-mode-mode_name .mode-mode_name-title {
  color: #80FF01;
}

.mode_list-mode-score {
  width: 100%;
  height: 40%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.infinite_mode_list-mode .mode_list-mode-score {
  border-top: 0.2vh solid;
  border-image: linear-gradient(
    175deg,
    #00000000 15%,
    #fd61fd 70%
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.classic_mode_list-mode .mode_list-mode-score {
  border-top: 0.2vh solid;
  border-image: linear-gradient(
    175deg,
    #00000000 15%,
    #c9faab 70%
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.gravity_mode_list-mode .mode_list-mode-score {
  border-top: 0.2vh solid;
  border-image: linear-gradient(
    175deg,
    #00000000 15%,
    #8eeffe 70%
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.hundred_mode_list-mode .mode_list-mode-score {
  border-top: 0.2vh solid;
  border-image: linear-gradient(
    175deg,
    #00000000 15%,
    #ffe3ae 70%
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.survival_mode_list-mode .mode_list-mode-score {
  border-top: 0.2vh solid;
  border-image: linear-gradient(
    175deg,
    #00000000 15%,
    #c9faab 70%
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.mode_list-mode-score .mode-score_icon {
  height: 3vh;
  margin-right: 3vh;
}
.mode_list-mode-score .mode-score_value {
  margin-right: 3vh;
  font-size: 3vh;
}
.mode_list-mode_icon {
  position: absolute;
  height: 90%;
  left: 10%;
  top: 5%;
}
