.store_popup-title {
  border: 1vh solid;
  border-image: linear-gradient(
    90deg,
    #fed003,
    #8059ff,
    #fe7333
  ); /* Градиент для обводки */
  border-image-slice: 1;
}
.store_popup-nav_panel {
  justify-content: space-around;
}
.packages_sub_tab {
  letter-spacing: .1vh;
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 100%;
  justify-content: space-evenly;
}
.packages-free_rubies {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 7vh;
  background: url('../sources/bg-gradient-1.png');
  background-size: 100% 100%;
  border: .3vh solid;
  border-image: linear-gradient(
    150deg,
    #FF52DC,
    #8059FF,
    #CD0082
  ); /* Градиент для обводки */
  border-image-slice: 1;
  /* backdrop-filter: blur(20px); */
  backdrop-filter: drop-shadow(0 24px 10px #00000029);
}
.packages-package {
  width: 100%;
  height: 16vh;
  background: url('../sources/bg-gradients.png');
  background-size: 100% 100%;
  border: .3vh solid;
  border-image: linear-gradient(
    #f3b6ffe9,
    #15151500,
    #C095C94A
  ); /* Градиент для обводки */
  border-image-slice: 1;
  /* backdrop-filter: blur(19px); */
}
.packages-package .package-info {
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.packages-package .package-info .info-rubies{
  height: 100%;
  margin-left: 4vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.packages-package .package-info .info-bonuses{
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #220663;
  padding: 1vh;
  margin-right: 1vh;
  position: relative;
}
.packages-package .package-info .info-bonuses .bonuses-bonus_element{
  height: 4.5vh;
  width: 4.5vh;
  margin: .5vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: .1vh solid;
  border-image: radial-gradient(
    #FFFFFF,
    #15151500,
    #95A3C94A
  ); /* Градиент для обводки */
  border-image-slice: 1;
  position: relative;
}

.packages-package .package-info .info-bonuses .bonuses-bonus_element .bonus_element-img{
  width: 100%;
  height: 100%;
}
.packages-package .package-info .info-bonuses .bonuses-bonus_element .bonus_element-title{
  position: absolute;
  bottom: .1vh;
  font-size: 1.5vh;
}
.packages-package .package-price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #220663;
  height: 40%;
  position: relative;
}
.packages-package .package-info .info-bonuses::before,
.packages-package .package-price::before {
  content: '';
  position: absolute;
  inset: 0; /* Заполняет весь контейнер */
  background: #ffffff10; /* Маска с прозрачностью */
  pointer-events: none; /* Маска не блокирует клики */
  z-index: 1; /* Маска поверх фона */
}
.packages-package .package-price .price-title{
  font-size: 1.7vh;
  margin-left: 2vh;
}
.packages-package .package-info .info-rubies .info-value {
  font-size: 2.5vh;
  margin-top: -.9vh;
}
.free_rubies-img {
  height: 100%;
}
.free_rubies-title {
  color: #ffffff;
  font-size: 2vh;
}
.free_rubies-btn {
  background-color: #8dfe01;
  width: 13vh;
  height: 4vh;
  padding: 0 1vh 0 1.5vh;
  border: none;
  border-bottom: #476d43 .4vh solid;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 1vh;
  font-size: 2.5vh;
}
.price-btn {
  background-color: #8dfe01;
  width: 13vh;
  height: 4vh;
  border: none;
  border-bottom: #476d43 .4vh solid;
  margin-right: 1vh;
  font-size: 2.5vh;
}

.rubies-img {
  height: 7vh;
}
.bonus_element-img {
  height: 4vh;
}
.ads_btn-img {
  height: 100%;
}

.popup-nav_panel-nav_element {
  color: #FFFFFF80;
}
.popup-nav_panel-nav_element.active {
  color: #FED003;
  text-shadow: 0 0 1vh #FED003;
}
.ball_collection_element {
  border: .3vh solid;
  border-image: linear-gradient(
    #f3b6ffe9,
    #15151500,
    #C095C94A
  ); /* Градиент для обводки */
  border-image-slice: 1;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.ball_collection_element-active {
  border: .3vh solid;
  border-image: linear-gradient(
    #ffffff,
    #f3b6ffe9 30%
  ); /* Градиент для обводки */
  border-image-slice: 1;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.ball_collection_element-buy_btn {
  background-color: #8dfe01;
  border: none;
  border-bottom: #476d43 .4vh solid;
}
.collection_element-size {
  font-weight: normal;
  font-size: 1.6vh;
  margin-bottom: .5vh;
}