.body {
    height: 100%;
    max-height: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button {
    padding: 10px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    margin-top: 10px;
    transition: background-color 0.3s ease;
}

.absolute-btn-1 {
    position: absolute;
    bottom: 50px;
    left: 5px;
}

.absolute-btn-2 {
    position: absolute;
    bottom: 10px;
    left: 5px;
}