.tests_popup-title {
  border: 1vh solid;
  border-image: linear-gradient(90deg, #C9FF56, #517FFB, #8DFF00); /* Градиент для обводки */
  border-image-slice: 1;
}
.tests_popup_container .close_tab_btn {
  filter: drop-shadow(0 0 1.8vh #8DFF00);
}
.tests_popup_container {
  border: .3vh solid;
  border-image: radial-gradient(#FFFFFF, #15151500, #33FF004A);
  border-image-slice: 1;
}